import React, { Component } from 'react'
import Hobby from './Hobby'
import Introduction from './Introduction'
import Projects from './Projects'
import Skills from './Skills'
import { BrowserRouter,Link,Route } from 'react-router-dom';
import ContactPage from './ContactPage'
export default class Routing extends Component {
    render() {
        return (
            <>
            
            <Route exact path="/ResumeDeploy/intro" component={Introduction} />
            <Route exact path="/ResumeDeploy/skills" component={Skills} />
            <Route exact path="/ResumeDeploy/projects" component={Projects} />
            <Route exact path="/ResumeDeploy/hobby" component={Hobby} />
            <Route exact path="/ResumeDeploy/contact" component={ContactPage} />
            <Route exact path="/ResumeDeploy/" component={Introduction} />
           
            </>
        )
    }
}
